.sidebarOption{
    display: flex;
    color: grey;
    height: 40px;
    cursor: pointer;
    transition: 200ms color erase-in;
    align-items: center;
}

.sidebar > hr{
    border:1px solid #282828;
    width:90%;
    margin: 10px auto;
}

.sidebarOption:hover{
    color: white;
}

.sidebarOption__icon{
    padding-left: 10px;
    padding-right: 20px;
}
.sidebarOption > p{
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}

