.login{
    display: grid;
    place-items: center;
    height: 100vh;
    background-color: black;
}
.login > img{
    width: 100%;
}
.login > a{
    padding: 20px;
    border-radius: 99px;
    background-color: green;
    font-weight: 800;
    color: white;
    text-decoration: none;
}



